body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: yellowgreen;
  font-family: cursive;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: wheat;
  border: 5px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 4rem;
  height: 4rem;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.game-board {
  display: flex;
  flex-direction: row;
  transform: translateX(1rem);
  gap: 2rem;
}
.game-info {
  margin-left: 20px;
}

li > button {
  padding: 0.5rem;
  margin-top: 0.5rem;
  font-weight: bold;
}

@media screen and (max-width: 600px) {
  .game-board {
    flex-direction: column;
    transform: translateX(0);
  }
  .game-info {
    font-size: 1.5rem;
    line-height: 1rem;
  }
}
